@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,900;1,300;1,400;1,900&display=swap');


*,
*::before,
*::after {
    box-sizing: border-box;

}

html {
    scroll-behavior: smooth;
}

:root {

    --clr-icons: #FFFBFA;
    --clr-text: #fff;
    --clr-bg-dark: #051518;
    --clr-bg-light: #0E363F;
    --clr-accent: #FF9B0B;
    --main-fnt: 'Lato', sans-serif;

}


body {
    margin: 0 auto;
    font-family: var(--main-fnt);


    a {
        text-decoration: none;

    }
}

.geology__main-container {
    background-color: var(--clr-icons);
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-areas: 'header header header header'
                        'main main main main'
                        'footer footer footer footer'
    ;
}

header {
    grid-area: header;
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 100px;
    align-items: center;
    position: fixed;
    z-index: 100;
    top: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: var(--clr-icons);
    box-shadow: 0 2px 5px 5px rgba(0, 0, 0, 0.069);

    .logo {
        margin-left: 10px;
        font-size: 3rem;
    }

    button {
        margin-right: 10px;
    }

    nav {
        font-size: 1.5rem;
        margin-left: 45%;

        a {
            text-decoration: none;
            padding: 10px;
            color: var(--clr-bg-light);

            transition: 500ms;
            z-index: 3;
            :visited {
                color:  black;
            }

           

        }

        

      

    
    }

}


main {
    grid-area: main;
    width: 100%;
    display: grid;
    grid-template-areas: 
    'hero hero hero hero'
    'services services services services'
    'topics topics topics topics '
    'benefits benefits benefits benefits'
    'about about about about'
    ;

    margin-top: 25px;

}
/* Hero Box Style */
.geology__main-hero {

    display: grid;
    width:  100%;
    height: 100vh;
    grid-area: hero;
    overflow: hidden;

    grid-template-areas: 
    
    'info info images images'
    'info info images images'
    'stats stats images images'
    'stats stats images images'
    ;



    .hero-info {
            grid-area: info;
            width: 75vh;
            background-color: #FFFBFA;
            height: 70vh;
            display: flex;
            flex-flow: column nowrap; 
            padding: 50px;
            margin-left: 100px;

            h1 {
                margin-top: 130px;
                font-size: 5rem;
                width: 85%;
            }

            h2 {
                font-size: 2rem;
                width: 75%;
                color: rgb(124, 124, 124);


            }

            .btn-div {
                padding-top: 100px;
                width: 100%;
                display: flex;
                flex-flow: row nowrap;
                justify-content: space-between;
                margin: 0 auto;
                align-items: center;
                margin-left: 100px;
        
    


                .explore-btn {
                    width: 250px;
                    border: none;
                    background-color: var(--clr-accent);
                    padding-top: 20px;
                    padding-bottom: 20px;
                    font-size: 1.5rem;
                    border-radius: 5rem;
                    margin-right: 50px;
                    transition: 500ms;
                    font-weight: 600;

                    a {
                        color: #FFFBFA;
                    }
                }
    
                .explore-btn:hover {
                    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.225);
                    transform: translate(5px, -5px)
                }
                .play-btn {
                    display: flex;
                    align-items: center;
                    background-color: transparent;
                    width: 250px;
                    border: none;
                    padding-top: 10px;
                    padding-bottom: 10px;
                    font-size: 1.5rem;
                    font-weight: 600;
                    border-radius: 5rem;
                    margin-right: 50px;
                    transition: 500ms;

                    i {
                        padding: 10px;
                        color:#FF9B0B;
                        background-color: #0e363f4d;
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        font-size: 1.7rem;
                        transform: translateY(9px);

                    }

                    .border-circle {
                        left: 50%;
                        top: 50%;
                        width: 70px;
                        height: 70px;
                        border-radius: 50%;
                        border: 2px solid #FF9B0B;
                        background-color: transparent;
                        margin-right: 25px;
                        


                    }

                    
                    
                }

                .play-btn:hover {
                    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.225);
                    transform: translate(5px, -5px)
                }
            }
             
    }

    .color-box {
        grid-area: images;
        background-color: var(--clr-bg-light);
        width: 100%;
        height: 100vh;
        margin-top: 150px;
        position: relative;
        border-radius: 5rem 0 0 0;
        box-shadow: -5px 0px 5px 5px rgba(0, 0, 0, 0.118);
        

    }

    .img-box {
        width: 35vh;
        grid-area: images;
        background-image: url(/images/pexels-oladimeji-ajegbile-2325729.jpg);
        z-index: 10;
        height: 75vh;
        width: 55vh;
        background-position: center;
        background-size: cover;
        margin: 50px;
        margin-top: 20vh;
        margin-bottom: 15vh;
        box-shadow: 6px 5px 10px 5px rgba(0, 0, 0, 0.202);
        border-radius: .5rem;

    }



}


.stats-div {
    display: flex;
    flex-flow: row nowrap;
    grid-area: stats;
    justify-content: space-evenly;
    width: 100%;

    align-items: center;
    padding-bottom: 80px;
    align-items: center;


}


.stats-item {

    width: 200px;
    height: 300px;
    display: flex;
    flex-direction: column nowrap;
    color: #051518;
    justify-content: center;
    align-items: center;

    strong {
        font-size: 3rem;
        padding: 25px;
    }

    p {
        font-size: 1.7rem;
        padding-bottom: 15px;

    }
}



